import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  static async start() {
    let data;
    let dataViews;
    let clipsCount = 12;
    let position = 0;

    AppView.starterRender();

    const form = document.querySelector('form');
    const text = document.querySelector('input');

    const carousel = document.querySelector('.carousel');
    const list = document.querySelector('.images');
    const currentPage = document.querySelector('.currentPage');

    async function getClipsFromNextPage() {
      const request = text.value;
      const token = data.nextPageToken;
      data = await AppModel.getNewClipsInfo(request, token);
      dataViews = await AppModel.getClipViewsCount(data.items);
      AppView.addSlides(data.items, dataViews.items);
    }

    function checkNewClips() {
      const clipsOnPage = parseInt(window.getComputedStyle(document.querySelector('.gallery')).width, 10) / 250;
      const pagesCount = clipsCount / clipsOnPage;
      if (+(currentPage.innerHTML) + 1 === pagesCount) {
        getClipsFromNextPage();
        clipsCount += 12;
      }
    }

    carousel.querySelector('.prev').onclick = () => {
      position = Math.min(position + parseInt(window.getComputedStyle(document.querySelector('.gallery')).width, 10), 0);
      list.style.marginLeft = `${position}px`;
      AppModel.checkPage(-1);
      AppView.addPopUp('p');
    };

    carousel.querySelector('.next').onclick = () => {
      if (data) {
        position -= parseInt(window.getComputedStyle(document.querySelector('.gallery')).width, 10);
        list.style.marginLeft = `${position}px`;
        AppModel.checkPage(1);
        AppView.addPopUp('n');
        checkNewClips();
      }
    };

    window.addEventListener('resize', () => {
      currentPage.innerHTML = 1 + Math.ceil((-1) * position / parseInt(window.getComputedStyle(document.querySelector('.gallery')).width, 10));
    }, false);

    async function getClips() {
      const request = text.value;
      const animationIcon = document.createElement('i');
      animationIcon.className = 'fab fa-youtube';
      document.body.appendChild(animationIcon);
      data = await AppModel.getClipInfo(request);
      dataViews = await AppModel.getClipViewsCount(data.items);
      document.body.removeChild(animationIcon);
      carousel.style.display = 'flex';
      AppView.addSlides(data.items, dataViews.items);
    }

    form.onsubmit = () => {
      const content = document.querySelector('.images');
      while (content.firstChild) {
        content.removeChild(content.firstChild);
      }
      currentPage.innerHTML = 1;
      list.style.marginLeft = 0;
      position = 0;
      carousel.style.display = 'none';
      getClips();
      return false;
    };
  }
}
