export default class AppView {
  static starterRender() {
    const title = document.querySelector('title');
    title.innerHTML = 'YouTube Client';

    const meta = document.createElement('meta');
    meta.name = 'viewport';
    meta.content = 'width=device-width, initial-scale=1.0';
    document.getElementsByTagName('head')[0].appendChild(meta);

    const fontAwesomeLink = document.createElement('link');
    fontAwesomeLink.rel = 'stylesheet';
    fontAwesomeLink.href = 'https://use.fontawesome.com/releases/v5.8.2/css/all.css';
    fontAwesomeLink.crossorigin = 'anonymous';
    document.getElementsByTagName('head')[0].appendChild(fontAwesomeLink);

    const font = document.createElement('link');
    font.rel = 'stylesheet';
    font.href = 'https://fonts.googleapis.com/css?family=Noto+Sans+SC&display=swap';
    document.getElementsByTagName('head')[0].appendChild(font);

    const siteIcon = document.createElement('link');
    siteIcon.rel = 'shortcut icon';
    siteIcon.href = 'http://www.iconarchive.com/download/i98467/dakirby309/simply-styled/YouTube.ico';
    siteIcon.typ = 'image/x-icon';
    document.getElementsByTagName('head')[0].appendChild(siteIcon);

    const header = document.createElement('header');
    document.body.appendChild(header);

    const logo = document.createElement('img');
    logo.className = 'logo';
    logo.src = 'https://upload.wikimedia.org/wikipedia/commons/e/e1/Logo_of_YouTube_%282015-2017%29.svg';
    header.appendChild(logo);

    const form = document.createElement('form');

    const searchIcon = document.createElement('i');
    searchIcon.className = 'fas fa-search';

    header.appendChild(searchIcon);

    const text = document.createElement('input');
    text.type = 'text';
    text.placeholder = 'Search';

    form.appendChild(text);
    header.appendChild(form);

    const carousel = document.createElement('div');
    carousel.className = 'carousel';

    const buttonPrev = document.createElement('button');
    buttonPrev.className = 'arrow prev';
    buttonPrev.innerHTML = '⇦';

    const gallery = document.createElement('div');
    gallery.className = 'gallery';

    const list = document.createElement('ul');
    list.className = 'images';

    const buttonNext = document.createElement('button');
    buttonNext.className = 'arrow next';
    buttonNext.innerHTML = '⇨';

    const currentPage = document.createElement('span');
    currentPage.className = 'currentPage';
    currentPage.innerHTML = 1;

    gallery.appendChild(list);
    carousel.appendChild(buttonPrev);
    carousel.appendChild(gallery);
    carousel.appendChild(buttonNext);
    carousel.appendChild(currentPage);
    document.body.appendChild(carousel);
  }

  static addPopUp(pos) {
    const currentPage = document.querySelector('.currentPage');
    const popUp = document.createElement('span');
    popUp.className = 'popUp';
    popUp.innerHTML = currentPage.innerHTML;

    if (pos === 'p') popUp.style.left = '7px';
    else popUp.style.right = '7px';

    document.querySelector('.carousel').appendChild(popUp);
    setTimeout(() => { document.querySelector('.carousel').removeChild(popUp); }, 100);
  }

  static addSlides(clips, views) {
    const list = document.querySelector('.images');
    for (let i = 0; i < 12; i += 1) {
      const elem = document.createElement('li');
      const elemContent = document.createElement('div');
      elemContent.className = 'elem';

      const myfunc = () => {
        const mainInfo = document.createElement('div');
        mainInfo.className = 'mainInfo';

        const img = document.createElement('img');
        img.className = 'clipImage';
        img.src = clips[i].snippet.thumbnails.high.url;

        const ref = document.createElement('a');
        ref.innerHTML = clips[i].snippet.title;
        ref.href = `https://www.youtube.com/watch?v=${clips[i].id.videoId}`;
        ref.target = '_blank';

        mainInfo.appendChild(img);
        mainInfo.appendChild(ref);

        elemContent.appendChild(mainInfo);

        const additionalInfo = document.createElement('div');
        additionalInfo.className = 'additionalInfo';

        const clipInfo = document.createElement('div');
        clipInfo.className = 'clipInfo';

        const authorInfo = document.createElement('p');
        authorInfo.className = 'shortInfo';

        const userIcon = document.createElement('i');
        userIcon.className = 'fas fa-user-alt';

        const userName = document.createElement('span');
        userName.innerHTML = clips[i].snippet.channelTitle;

        authorInfo.appendChild(userIcon);
        authorInfo.appendChild(userName);
        clipInfo.appendChild(authorInfo);

        const dateInfo = document.createElement('p');
        dateInfo.className = 'shortInfo';

        const dateIcon = document.createElement('i');
        dateIcon.className = 'far fa-calendar-alt';

        const dateName = document.createElement('span');
        dateName.innerHTML = clips[i].snippet.publishedAt.substring(0, 10);

        dateInfo.appendChild(dateIcon);
        dateInfo.appendChild(dateName);
        clipInfo.appendChild(dateInfo);

        const viewsInfo = document.createElement('p');
        viewsInfo.className = 'shortInfo';

        const viewIcon = document.createElement('i');
        viewIcon.className = 'far fa-eye';

        const viewName = document.createElement('span');
        viewName.innerHTML = views[i].statistics.viewCount;

        viewsInfo.appendChild(viewIcon);
        viewsInfo.appendChild(viewName);
        clipInfo.appendChild(viewsInfo);

        const clipDescription = document.createElement('div');
        clipDescription.className = 'clipDescription';

        const description = document.createElement('p');
        description.className = 'description';
        description.innerHTML = clips[i].snippet.description;
        clipDescription.appendChild(description);


        additionalInfo.appendChild(clipInfo);
        additionalInfo.appendChild(clipDescription);
        elemContent.appendChild(additionalInfo);
      };
      myfunc();
      elem.appendChild(elemContent);
      list.appendChild(elem);
    }
  }
}
