export default class AppModel {
  static async getClipInfo(name) {
    const apiKey = 'AIzaSyDwMeq-DyctNGExLZ0UvRazM01yyteqsKE';
    const clipName = `https://www.googleapis.com/youtube/v3/search?key=${apiKey}&type=video&part=snippet&maxResults=12&q=${name}`;
    const responce = await fetch(clipName);
    const data = await responce.json();
    return data;
  }

  static async getClipViewsCount(name) {
    const videoIds = name.map(clip => clip.id.videoId).join(',');
    const apiKey = 'AIzaSyDwMeq-DyctNGExLZ0UvRazM01yyteqsKE';
    const request = `https://www.googleapis.com/youtube/v3/videos?key=${apiKey}&id=${videoIds}&part=snippet,statistics`;
    const responce = await fetch(request);
    const data = await responce.json();
    return data;
  }

  static async getNewClipsInfo(name, token) {
    const apiKey = 'AIzaSyDwMeq-DyctNGExLZ0UvRazM01yyteqsKE';
    const clipName = `https://www.googleapis.com/youtube/v3/search?key=${apiKey}&type=video&part=snippet&maxResults=12&q=${name}&pageToken=${token}`;
    const responce = await fetch(clipName);
    const data = await responce.json();
    return data;
  }

  static checkPage(arg) {
    const currentPage = document.querySelector('.currentPage');
    currentPage.innerHTML = Math.max(+(currentPage.innerHTML) + arg, 1);
  }
}
